<template>
	<div class="modal fade" id="quizModal" tabindex="-1" role="dialog" aria-labelledby="quizModalTitle" aria-hidden="true">
		<div class="modal-dialog" role="document">
			<div class="modal-content">
				<div class="modal-header modal__header">
					<h3 class="modal-title" id="contactModalTitle">Add Quiz</h3>
					<button type="button" class="close" data-dismiss="modal" aria-label="close">
						<span aria-hidden="true">&times;</span>
					</button>
				</div>
				<div class="modal-body modal__body">
					<form 
						@submit.prevent="createQuiz"
						class="modal__form" 
						method="post">
						<div class="form-group">
							<label class="modal__label" for="class-title">Title</label>
							<input 
								type="text" 
								:class="[{'is-invalid': errors.title}]"
								class="form-control" 
								id="quiz-title" 
								placeholder="Quiz Title"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.title">
							<div class="invalid-feedback">
								{{ errors.title }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="duration">Duration</label>
							<select 
								class="custom-select form-control"  
								id="category"
								:class="[{'is-invalid': errors.duration}]"
								v-model="itemData.duration">
								<option value="3600">1 hour</option>
								<option value="5400">1 hour 30 minutes</option>
								<option value="7200">2 hours</option>
								<option value="9000">2 hours 30 minutes</option>
								<option value="10800">3 hours</option>
								<option value="12600">3 hours 30 minutes</option>
								<option value="14400">4 hours</option>
								<option value="16200">4 hours 30 minutes</option>
								<option value="18000">5 hours</option>
							</select>
							<div class="invalid-feedback">
								{{ errors.duration }}
							</div>
						</div>
					</form>
				</div>
				<div class="modal-footer modal__footer">
					<Loader v-if="loading"/>
					<button 
						@click="clearErrors()"
						class="btn btn--md" 
						type="button"
						data-dismiss="modal"
						aria-label="close"
						name="button">Cancel</button>
					<button 
						@click.prevent="createQuiz"
						class="btn btn--md btn__primary" 
						type="submit" 
						name="button">
						Save Class</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import form from '../../mixins/form';
import Loader from '../../components/Loader';
import { createQuiz } from '../../utils/api/quiz';
import quiz from '../../utils/validations/quiz';
import { success, fail } from '../../utils/toast';
import eventHub from '../../utils/eventhub';

export default {
	name: 'AddTest',
	mixins: [form],
	data() {
		return {
			itemData: {},
			errors: {},
			loading: false
		};
	},
	watch: {
		issues(val) {
			this.errors = { ...val };
		}
	},
	methods: {
		createQuiz() {
			this.validate(this.itemData, quiz.create);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.loading = true;
			createQuiz(this.itemData)
				.then((response) => {
					if (response.status === 201) {
						this.itemData = {};
						this.loading = false;
						eventHub.$emit('quiz-created', response.data);
						success({
							title: 'Success! A new quiz has been created'
						});
					}
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Oops! Something went wrong'
					});
					return error;
				});
			return true;
		}
	},
	components: {
		Loader
	}
};
</script>
