import Joi from 'joi';

const quiz = {
	create: {
		title: Joi.string().min(2).required(),
		duration: Joi.number().integer().positive().required()
	},
	createResponse: {
		response: Joi.string().required(),
		report: Joi.string().required()
	},
	updateResponse: {
		response: Joi.string().required(),
		report: Joi.string().required()
	}
};

export default quiz;
