<template>
	<div>
		<template v-if="loadingQuiz">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<div class="main__header">
				<div class="main__primary">
					<h3 class="main__title">Tests</h3>
				</div>
				<template v-if="quizLoaded && quizzes.length < 1">
					<div class="d-flex">
						<button class="btn btn__primary" type="button" name="button"
						data-toggle="modal" data-target="#quizModal">
							<span class="btn__icon">
								<svg width="24" height="13" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 5H5V2H3v3H0v2h3v3h2V7h3V5zm10 1c1.66 0 2.99-1.34 2.99-3S19.66 0 18 0c-.32 0-.63.05-.91.14.57.81.9 1.79.9 2.86 0 1.07-.34 2.04-.9 2.86.28.09.59.14.91.14zm-5 0c1.66 0 2.99-1.34 2.99-3S14.66 0 13 0c-1.66 0-3 1.34-3 3s1.34 3 3 3zm6.62 2.16C20.45 8.89 21 9.82 21 11v2h3v-2c0-1.54-2.37-2.49-4.38-2.84zM13 8c-2 0-6 1-6 3v2h12v-2c0-2-4-3-6-3z" fill="#fff"/></svg>
							</span>Add Test</button>
					</div>
				</template>
			</div>
			<section class="main-grid__row contacts contacts--list">
				<div class="contacts--list__wrapper">
					<div class="contacts--list__item head">
						<div></div>
						<p>Basic info</p>
						<p>Actions</p>
					</div>
					<div class="contacts__body">
						<template v-if="quizLoaded && quizzes.length">
							<TestItem
								v-for="quiz in quizzes"
								:key="quiz.id"
								:quiz-data="quiz"/>
						</template>
						<template v-else-if="quizLoaded">
							<p>No quiz found</p>
						</template>
						<template v-else>
							<p>An error occurred while loading quiz, please try again.</p>
						</template>
					</div>
				</div>
			</section>
		</template>
        <Pagination
			:options="{ limit, offset, total, baseUrl }"
			v-if="total > limit"
		/>
		<AddTest />
	</div>
</template>

<script>
import Loader from '../../components/Loader';
import { getQuizzes } from '../../utils/api/quiz';
import AddTest from './AddTest';
import TestItem from './TestItem';
import eventHub from '../../utils/eventhub';

export default {
	name: 'Index',
	data() {
		return {
			quizzes: {},
			quizLoaded: false,
			loadingQuiz: true,
			baseUrl: 'quiz',
			offset: 0,
			limit: 6,
			total: 0
		};
	},
	created() {
		this.limit = this.$route.query.limit ? this.$route.query.limit : this.limit;
		this.offset = this.$route.query.offset ? this.$route.query.offset : this.offset;
		this.getQuizzes();
		/** For reactivity */
		eventHub.$on('quiz-created', (payload) => {
			this.quizzes.unshift(payload);
		});
	},
	components: {
		TestItem,
		Loader,
		AddTest
	},
	methods: {
		getQuizzes() {
			this.loadingQuiz = true;
			getQuizzes(this.limit, this.offfset)
				.then((response) => {
					if (response.status === 200) {
						this.quizLoaded = true;
						this.quizzes = response.data.data;
					}
				})
				.catch((error) => {
					this.quizLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingQuiz = false;
				});
			return true;
		}
	},
	watch: {
		$route() {
			this.limit = this.$route.query.limit ? this.$route.query.limit : 10;
			this.offset = this.$route.query.offset ? this.$route.query.offset : 0;
			this.getQuizzes();
		}
	}
};
</script>

